<template><section>

<h1>Icon 图标</h1>
<hr>
<p>语义化的矢量图形，<code v-pre class="code_inline">qiucode-UI</code> 使用开源的 Iconfont (阿里妈妈MUX倾力打造的矢量图标管理、交流平台) 作为图标库，并制作成了 <code v-pre class="code_inline">icon font</code>。</p>
<h3>如何使用</h3>
<p>使用 <code v-pre class="code_inline">type=&quot;icon&quot;</code> 来声明图标，具体图标的名称请 <code v-pre class="code_inline">copy</code> 相应的标签</p>
<div class="demo-block">
  <qiu-icon type="icon-close" size="18" color="red"></qiu-icon>
  <qiu-icon type="icon-check" size="18"></qiu-icon>
  <qiu-icon type="icon-tag" size="18"></qiu-icon>
  <qiu-icon type="icon-share" size="18"></qiu-icon>
</div>
<demo-block>
                        <div slot="desc">  <qiu-icon type="icon-close" size="18" color="red"></qiu-icon>
  <qiu-icon type="icon-check" size="18"></qiu-icon>
  <qiu-icon type="icon-tag" size="18"></qiu-icon>
  <qiu-icon type="icon-share" size="18"></qiu-icon>
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-close"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"18"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"red"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">qiu-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-check"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"18"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">qiu-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-tag"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"18"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">qiu-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-share"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"18"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">qiu-icon</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>API</h2>
<h4>qiu-icon属性</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>尺寸，图标的大小</td>
<td>string/Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>颜色，16进制颜色值或其他都可以</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>图标，已有的图标库中的图标名</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>图标示例</h3>
<ul class="icon-list">
  <li v-for="name in icons" :key="name">
    <span>
      <i :class="'iconfont '+name"></i>
      {{name}}
    </span>
  </li>
</ul>
</section></template>
<style lang="less">
  .demo-icon .source > i {
    font-size: 24px;
    color: #8492a6;
    margin: 0 20px;
    font-size: 1.5em;
    vertical-align: middle;
  }
  
  .demo-icon .source > button {
    margin: 0 20px;
  }

  .icon-list {
    overflow: hidden;
    list-style: none;
    padding: 0;
    border: solid 1px #eaeefb;
    border-radius: 4px;
  }
  .icon-list li {
    float: left;
    width: 16.66%;
    text-align: center;
    height: 120px;
    line-height: 120px;
    color: #666;
    font-size: 13px;
    transition: color .15s linear;

    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-right: -1px;
    margin-bottom: -1px;
    span {
      display: inline-block;
      line-height: normal;
      vertical-align: middle;
      font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
      color: #99a9bf;
    }
    i {
      display: block;
      font-size: 32px;
      margin-bottom: 15px;
      color: #3f536e;
    }
    &:hover {
      color: rgb(92, 182, 255);
    }
  }
</style>
<script>
  var iconList = require('../icon.json');

  export default {
    data() {
      return {
        icons: iconList
      };
    }
  }
</script>


