<template><section><h1>Input 输入框</h1>
<hr>
<h3>概述</h3>
<p>基本表单组件，支持 input 和 textarea，并在原生控件基础上进行了功能扩展，可以组合使用。</p>
<hr>
<h3>基础用法</h3>
<p>基本用法，可以使用 v-model 实现数据的双向绑定。可以直接设置 style 来改变输入框的宽度，默认 100%。</p>
<template>
    <QiuInput v-model="value" placeholder="Enter something..." style="width: 300px">
</QiuInput></template>

<demo-block>
                        <div slot="desc"><template>
    <qiuinput v-model="value" placeholder="Enter something..." style="width: 300px">
</qiuinput></template>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter something..."</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 300px"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">value</span>: <span class="hljs-string">''</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>不同尺寸</h3>
<p>输入框有三种尺寸：大、默认（中）、小
通过设置size为large和small设置为大和小尺寸，不设置为默认（中）尺寸。</p>
<div>
    <span>
      <QiuInput size="large" placeholder="large size" style="width: 200px">
    </QiuInput></span>
    <span>
       <QiuInput placeholder="default size" style="width: 200px">
    </QiuInput></span>
    <span>
       <QiuInput size="small" placeholder="small size" style="width: 200px">
    
</QiuInput></span></div>
<demo-block>
                        <div slot="desc">
  <qiuinput size="large" placeholder="large size">
  <br>
  <qiuinput placeholder="default size">
  <br>
  <qiuinput size="small" placeholder="small size">
</qiuinput></qiuinput></qiuinput></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"large size"</span> /&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"default size"</span> /&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"small size"</span> /&gt;</span>
</code></pre>
</div></demo-block>
<h3>可清空</h3>
<p>开启属性 clearable 可显示清空按钮。</p>
<div>
    <QiuInput placeholder="Enter something..." clearable style="width: 200px">
</QiuInput></div> 
<demo-block>
                        <div slot="desc"><qiuinput placeholder="Enter something..." clearable style="width: 200px">
</qiuinput></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter something..."</span> <span class="hljs-attr">clearable</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 200px"</span> /&gt;</span>
</code></pre>
</div></demo-block>
<h3>文本域</h3>
<p>通过设置属性 type 为 textarea 来使用文本域，用于多行输入。
通过设置属性 rows 控制文本域默认显示的行数。
设置属性 autosize，文本域会自动适应高度的变化。
autosize也可以设定为一个对象，指定最小行数和最大行数。</p>
<div>
    <span>
       <QiuInput type="textarea" placeholder="Enter something..." style="width:200px">
    </QiuInput></span>
    <span>
       <QiuInput type="textarea" :rows="4" placeholder="Enter something..." style="width:200px;">
    </QiuInput></span>
    <span>
       <QiuInput type="textarea" :autosize="true" placeholder="autosize" style="width:200px;">
    </QiuInput></span>
    <span>
       <QiuInput type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="minRows: 2,maxRows: 5" style="width:200px;">
    </QiuInput></span>
</div>
<demo-block>
                        <div slot="desc">
<template>
  <div>
    <qiuinput type="textarea" placeholder="Enter something...">
    <qiuinput type="textarea" :rows="4" placeholder="Enter something...">

    <qiuinput type="textarea" :autosize="true" placeholder="autosize">
    <qiuinput type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="minRows: 2,maxRows: 5">
  </qiuinput></qiuinput></qiuinput></qiuinput></div>
</template>


</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter something..."</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter something..."</span> /&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">:autosize</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"autosize"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">:autosize</span>=<span class="hljs-string">"{minRows: 2,maxRows: 5}"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"minRows: 2,maxRows: 5"</span> /&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>前缀和后缀图标</h3>
<p>除了设置 icon，也可以通过设置 prefix 和 suffix 及同名 slot 设置前缀及后缀图标。</p>
 <div style="width:50%">
    属性方式：<br>
    <span>
       <QiuInput prefix="yonghutouxiang" placeholder="Enter name" style="width: auto">
    </QiuInput></span>
    <QiuInput suffix="search" placeholder="Enter text" style="width: auto">
  </QiuInput></div>
  <div style="width:50%">
     <div style="margin-top: 6px">
        Slots方式：<br>
        <QiuInput placeholder="Enter name" style="width: auto">
            <QiuIcon type="icon-yonghutouxiang" slot="prefix">
        </QiuIcon></QiuInput>
        <QiuInput placeholder="Enter text" style="width: auto">
            <QiuIcon type="icon-search" slot="suffix">
        </QiuIcon></QiuInput>
    </div>
  </div>
<demo-block>
                        <div slot="desc">
<template>
    <div style="width:50%">
    属性方式：<br>
    <span>
       <qiuinput prefix="yonghutouxiang" placeholder="Enter name" style="width: auto">
    </qiuinput></span>
    <qiuinput suffix="search" placeholder="Enter text" style="width: auto">
  </qiuinput></div>

  <div style="width:50%">
     <div style="margin-top: 6px">
        Slots方式：<br>
        <qiuinput placeholder="Enter name" style="width: auto">
            <qiuicon type="icon-yonghutouxiang" slot="prefix">
        </qiuicon></qiuinput>
        <qiuinput placeholder="Enter text" style="width: auto">
            <qiuicon type="icon-search" slot="suffix">
        </qiuicon></qiuinput>
    </div>
  </div>
</template>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:50%"</span>&gt;</span>
    属性方式：<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>
       <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span> <span class="hljs-attr">prefix</span>=<span class="hljs-string">"yonghutouxiang"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter name"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: auto"</span> /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span> <span class="hljs-attr">suffix</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter text"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: auto"</span> /&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:50%"</span>&gt;</span>
     <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-top: 6px"</span>&gt;</span>
        Slots方式：<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter name"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: auto"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">QiuIcon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-yonghutouxiang"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prefix"</span> /&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">QiuInput</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Enter text"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: auto"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">QiuIcon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-search"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"suffix"</span> /&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">QiuInput</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>复合型输入框</h3>
<p>通过前置和后置的 slot 可以实现复合型的输入框。</p>
<div style="width:50%">
    <QiuInput>
        <span slot="prepend">http://</span>
        <span slot="append">.com</span>
    </QiuInput>
</div>
<demo-block>
                        <div slot="desc">
<template>
  <div style="width:50%">
    <qiuinput>
        <span slot="prepend">http://</span>
        <span slot="append">.com</span>
    </qiuinput>
  </div>
</template>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:50%"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuInput</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>http://<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">QiuInput</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2>API</h2>
<h4>QiuInput属性</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>输入框类型，可选值为 <code v-pre class="code_inline">text</code>、<code v-pre class="code_inline">password</code>、<code v-pre class="code_inline">textarea</code>、<code v-pre class="code_inline">url</code>、<code v-pre class="code_inline">email</code>、<code v-pre class="code_inline">date</code>、<code v-pre class="code_inline">number</code>、<code v-pre class="code_inline">tel</code></td>
<td>String</td>
<td>text</td>
</tr>
<tr>
<td>value</td>
<td>绑定的值，可使用 v-model 双向绑定</td>
<td>String/Number</td>
<td>空</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸，可选值为<code v-pre class="code_inline">large</code>、<code v-pre class="code_inline">small</code>、<code v-pre class="code_inline">default</code>或者不设置</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位文本</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>clearable</td>
<td>是否显示清空按钮</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>设置输入框为禁用状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>设置输入框为只读</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>maxlength</td>
<td>最大输入长度</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>输入框尾部图标，仅在 text 类型下有效</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>prefix</td>
<td>输入框头部图标</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>suffix</td>
<td>输入框尾部图标</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>search</td>
<td>是否显示为搜索型输入框</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>enter-button</td>
<td>开启 search 时可用，是否有确认按钮，可设为按钮文字</td>
<td>Boolean/String</td>
<td>false</td>
</tr>
<tr>
<td>rows</td>
<td>文本域默认行数，仅在 textarea 类型下有效</td>
<td>Number</td>
<td>2</td>
</tr>
<tr>
<td>autosize</td>
<td>自适应内容高度，仅在 textarea 类型下有效，可传入对象，如 { minRows: 2, maxRows: 6 }</td>
<td>Boolean/Object</td>
<td>false</td>
</tr>
<tr>
<td>number</td>
<td>将用户的输入转换为 Number 类型</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autofocus</td>
<td>自动获取焦点</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autocomplete</td>
<td>原生的自动完成功能</td>
<td>String</td>
<td>off</td>
</tr>
<tr>
<td>element-id</td>
<td>给表单元素设置 id，详见 Form 用法。</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>spellcheck</td>
<td>原生的 spellcheck 属性</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>wrap</td>
<td>原生的 wrap 属性，可选值为 hard 和 soft，仅在 textarea 下生效</td>
<td>String</td>
<td>soft</td>
</tr>
</tbody>
</table>
<h3>QiuInput 事件</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-enter</td>
<td>按下回车键时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-click</td>
<td>设置 icon 属性后，点击图标时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-change</td>
<td>数据改变时触发</td>
<td>event</td>
</tr>
<tr>
<td>on-focus</td>
<td>输入框聚焦时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-blur</td>
<td>输入框失去焦点时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-keyup</td>
<td>原生的 keyup 事件</td>
<td>event</td>
</tr>
<tr>
<td>on-keydown</td>
<td>原生的 keydown 事件</td>
<td>event</td>
</tr>
<tr>
<td>on-keypress</td>
<td>原生的 keypress 事件</td>
<td>event</td>
</tr>
<tr>
<td>on-search</td>
<td>开启 search 时可用，点击搜索或按下回车键时触发</td>
<td>value</td>
</tr>
<tr>
<td>on-clear</td>
<td>开启 clearable 时可用，点击清空按钮时触发</td>
<td>无</td>
</tr>
</tbody>
</table>
<h4>QiuInput Slots</h4>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>prepend</td>
<td>前置内容，仅在 text 类型下有效</td>
</tr>
<tr>
<td>append</td>
<td>后置内容，仅在 text 类型下有效</td>
</tr>
<tr>
<td>prefix</td>
<td>输入框头部图标</td>
</tr>
<tr>
<td>suffix</td>
<td>输入框尾部图标</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data () {
            return {
                value: ''
            }
        }
    }
</script>


