<template><section><h1>Radio 单选框</h1>
<hr>
<h3>基本单选框</h3>
<p>每个都是独立的单选按钮，各自需要单独绑定一个 model</p>
<template>
    <QiuRadio v-model="baseRadio" label="1">选项一</QiuRadio>
    <QiuRadio v-model="baseRadio" label="2">选项二</QiuRadio>
</template>
<demo-block>
                        <div slot="desc"><template>
    <qiuradio v-model="baseRadio" label="1">选项一</qiuradio>
    <qiuradio v-model="baseRadio" label="2">选项二</qiuradio>
</template>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"baseRadio"</span>  <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>选项一<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"baseRadio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>选项二<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">baseRadio</span>: <span class="hljs-string">''</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>单选框组合</h3>
<p>如果按钮存在互斥的情况，可用单选框组，但如果选项过多，建议使用下拉框组件。使用组合：QiuRadioGroup 和 QiuRadio</p>
<QiuRadioGroup v-model="radio3">
  <QiuRadio label="1">选项一</QiuRadio>
  <QiuRadio label="2">选项二</QiuRadio>
  <QiuRadio label="3">选项三</QiuRadio>
</QiuRadioGroup>
<demo-block>
                        <div slot="desc">
 <qiuradiogroup v-model="radio3">
  <qiuradio label="1">选项一</qiuradio>
  <qiuradio label="2">选项二</qiuradio>
  <qiuradio label="3">选项三</qiuradio>
</qiuradiogroup>


</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
 <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadioGroup</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio3"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>选项一<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadio</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>选项二<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadio</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"3"</span>&gt;</span>选项三<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadioGroup</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">radio3</span>:<span class="hljs-string">''</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>组合按钮</h3>
<p>按钮样式的单选框组合，使用组合：QiuRadioGroup 和 QiuRadioButton</p>
<QiuRadioGroup v-model="radio4">
  <QiuRadioButton label="北京">北京</QiuRadioButton>
  <QiuRadioButton label="上海" disabled>上海</QiuRadioButton>
  <QiuRadioButton label="深圳">深圳</QiuRadioButton>
  <QiuRadioButton label="秋码记录">秋码记录</QiuRadioButton>
</QiuRadioGroup>
<demo-block>
                        <div slot="desc">
<template>
  <qiuradiogroup v-model="radio4">
    <qiuradiobutton label="北京">北京</qiuradiobutton>
    <qiuradiobutton label="上海" disabled>上海</qiuradiobutton>
    <qiuradiobutton label="深圳">深圳</qiuradiobutton>
    <qiuradiobutton label="秋码记录">秋码记录</qiuradiobutton>
  </qiuradiogroup>
</template>



</div>
