    <span class="hljs-tag">&lt;<span class="hljs-name">QiuRadioButton</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"北京"</span>&gt;</span>北京<span class="hljs-tag">&lt;/<span class="hljs-name">QiuRadioButton</span>&gt;</span>
