</div></demo-block>
<h3>自定义组合按钮样式</h3>
<p>设置 fill 属性可更改选中按钮的背景颜色，设置 text-color 属性可更改选中按钮字体的颜色</p>
<template>
    <div>
        <p class="demo-desc">更换背景色</p>
        <div class="row">
            <QiuRadioGroup v-model="radio5" fill="#FF6464">
                <QiuRadioButton label="北京">北京</QiuRadioButton>
                <QiuRadioButton label="上海" disabled>上海</QiuRadioButton>
                <QiuRadioButton label="深圳">深圳</QiuRadioButton>
                <QiuRadioButton label="同一地方">同一地方</QiuRadioButton>
            </QiuRadioGroup>
        </div>
        <p class="demo-desc">更换字体颜色</p>
        <div class="row">
            <QiuRadioGroup v-model="radio5" text-color="#4C5D73">
                <QiuRadioButton label="北京">北京</QiuRadioButton>
                <QiuRadioButton label="上海" disabled>上海</QiuRadioButton>
                <QiuRadioButton label="深圳">深圳</QiuRadioButton>
                <QiuRadioButton label="同一地方">同一地方</QiuRadioButton>
            </QiuRadioGroup>
        </div>
    </div>
</template>
