<h3>不可用的多选框</h3>
<p>添加 disabled 属性禁用多选框</p>
<div>
    <p style="margin-bottom: 16px;">
        <QiuCheckbox v-model="checked" :disabled="disabled" @change="onChange">
            <span v-if="!checked">取消</span>
            <span v-if="checked">选中</span>
            -
            <span v-if="!disabled">可用</span>
            <span v-if="disabled">不可用</span>
        </QiuCheckbox>
    </p>
    <p>
       <QiuButton type="primary" size="small" @click="checked = !checked">
            <span v-if="checked">取 消</span>
            <span v-if="!checked">选 中</span>
       </QiuButton>
        <QiuButton type="primary" size="small" style="margin-left: 10px;" @click="disabled = !disabled">
            <span v-if="disabled">可用</span>
            <span v-if="!disabled">不可用</span>
        </QiuButton>
    </p>
</div>
<demo-block>
