</div></demo-block>
<h3>多选框组</h3>
<p>适用于多个多选框绑定同一个 model 的情景</p>
<div>
    <p>
    <QiuCheckbox :indeterminate="indeterminate" v-model="allChecked" @click="checkAll">全选</QiuCheckbox>
    </p>
    <p style="margin-bottom: 16px;">
        <QiuCheckboxGroup :data="options" v-model="fruits" @change="setState"></QiuCheckboxGroup>
    </p>
</div>
<demo-block>
                        <div slot="desc"><template>
   <div>
        <p>
        <qiucheckbox :indeterminate="indeterminate" v-model="allChecked" @click="checkAll">全选</qiucheckbox>
        </p>
        <p style="margin-bottom: 16px;">
            <qiucheckboxgroup :data="options" v-model="fruits" @change="setState"></qiucheckboxgroup>
        </p>
    </div>
</template>



</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
