</div></demo-block>

<h2>API</h2>
<h4>QiuCheckbox属性</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定当前是否选中</td>
<td>Any</td>
<td>false</td>
</tr>
<tr>
<td>true-value</td>
<td>选中时的值</td>
<td>Any</td>
<td>true</td>
</tr>
<tr>
<td>false-value</td>
<td>没有选中时的值</td>
<td>Any</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>只读，无法进行交互</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>设置 indeterminate 状态，只负责样式控制</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h4>QiuCheckboxGroup属性</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>默认选中的选项</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>keyField</td>
<td>选项的value的字段名</td>
<td>String</td>
<td>value</td>
</tr>
<tr>
<td>label</td>
<td>选项显示的文本的字段名</td>
<td>String</td>
<td>label</td>
</tr>
<tr>
<td>data</td>
<td>选项</td>
<td>Array</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>max</td>
<td>控制checkbox组最多的选中数量</td>
<td>Number</td>
<td>-</td>
</tr>
</tbody>
</table>
<h4>Data属性</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选项的值(该字段可通过keyField属性修改)</td>
<td>String</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>选项的标签(可通过label属性修改)</td>
<td>String</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>QiuCheckbox 事件</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>值发生变化的时候触发</td>
<td>value</td>
</tr>
<tr>
<td>click</td>
<td>点击的时候触发</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>QiuCheckboxGroup 事件</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>选择的值发生变化的时候触发</td>
<td>value</td>
</tr>
<tr>
<td>item-change</td>
<td>单项选择的值发生变化的时候触发</td>
<td>value数组中对应的值</td>
</tr>
<tr>
<td>item-click</td>
<td>点击单项的时候触发</td>
<td>value</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    name: 'checkAll',
    data() {
        return {
            value:'',
            checked: false,
            disabled: false,
            indeterminate: true,
            allChecked: false,
            fruits: ['Apple', 'Orange'],
            options: [
                {label: '苹果', value: 'Apple'},
                {label: '梨', value: 'Pear'},
                {label: '橘', value: 'Orange'},
            ],
            allFruits: ['Apple', 'Pear', 'Orange']
        }
    },
    methods: {
         onChange(value) {
            console.log("checkbox change event!!! " + value);
        },
        checkAll() {
            if (this.fruits.length == this.options.length) {
                this.fruits = [];
                this.allChecked = false;
                this.indeterminate = false;
            } else {
                this.fruits = JSON.parse(JSON.stringify(this.allFruits));
                this.allChecked = true;
                this.indeterminate = false;
            }
        },
        setState() {
            this.indeterminate =  this.fruits.length > 0 && this.fruits.length < this.options.length;
            this.allChecked = this.fruits.length == this.options.length;
        }
    }
  }
</script>


