<template><section>
<h1>QiuTag 标签</h1>
<hr>
<h3>基础使用</h3>
<p>标签的默认样式与可关闭的标签，添加<code v-pre class="code_inline">closable</code>可关闭标签</p>
<div class="demo-block">
  <qiu-tag>标签一</qiu-tag>
  <qiu-tag closable>标签二</qiu-tag>
</div>
<demo-block>
                        <div slot="desc">  <qiu-tag>标签一</qiu-tag>
  <qiu-tag closable>标签二</qiu-tag>
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span>&gt;</span>标签一<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">closable</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>标签颜色定义</h3>
<p>可使用<code v-pre class="code_inline">color</code>属性定义不同的标签颜色样式</p>
<div class="demo-block">
  <qiu-tag color="primary">标签一</qiu-tag>
  <qiu-tag color="success">标签二</qiu-tag>
  <qiu-tag color="warning">标签三</qiu-tag>
  <qiu-tag color="error">标签四</qiu-tag>
  <br><br>
  <qiu-tag color="blue">标签五</qiu-tag>
  <qiu-tag color="green">标签六</qiu-tag>
  <qiu-tag color="yellow">标签七</qiu-tag>
  <qiu-tag color="red">标签八</qiu-tag>
  <br>
  <qiu-tag closable type="border" color="primary">标签10</qiu-tag>
  <qiu-tag closable type="border" color="success">标签11</qiu-tag>
  <qiu-tag closable type="border" color="warning">标签12</qiu-tag>
  <qiu-tag closable type="border" color="error">标签13</qiu-tag>
</div>
<demo-block>
                        <div slot="desc">  <qiu-tag color="primary">标签一</qiu-tag>
  <qiu-tag color="success">标签二</qiu-tag>
  <qiu-tag color="warning">标签三</qiu-tag>
  <qiu-tag color="error">标签四</qiu-tag>
  <qiu-tag color="blue">标签五</qiu-tag>
  <qiu-tag color="green">标签六</qiu-tag>
  <qiu-tag color="yellow">标签七</qiu-tag>
  <qiu-tag color="red">标签八</qiu-tag>
  <qiu-tag closable type="border" color="primary">标签10</qiu-tag>
  <qiu-tag closable type="border" color="success">标签11</qiu-tag>
  <qiu-tag closable type="border" color="warning">标签12</qiu-tag>
  <qiu-tag closable type="border" color="error">标签13</qiu-tag>
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>标签一<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>标签三<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"error"</span>&gt;</span>标签四<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"blue"</span>&gt;</span>标签五<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"green"</span>&gt;</span>标签六<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"yellow"</span>&gt;</span>标签七<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"red"</span>&gt;</span>标签八<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"border"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>标签10<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"border"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>标签11<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"border"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>标签12<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"border"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"error"</span>&gt;</span>标签13<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>添加多个标签</h3>
<div class="demo-block">
  <qiu-tag v-for="tag in tags" :key="tag" :name="tag" closable @on-close="closeTag">标签{{tag+1}}</qiu-tag>
  <qiu-button icon="icon-plus" size="small" @click="addTag">添加标签</qiu-button>
</div>
<demo-block>
                        <div slot="desc"><template>
  <qiu-tag v-for="tag in tags" :key="tag" :name="tag" closable on-close="closeTag">标签{{tag+1}}</qiu-tag>
  <qiu-button icon="icon-plus" size="small" @click="addTag">添加标签</qiu-button>
</template>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-tag</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"tag in tags"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"tag"</span> <span class="hljs-attr">:name</span>=<span class="hljs-string">"tag"</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">on-close</span>=<span class="hljs-string">"closeTag"</span>&gt;</span>标签{{tag+1}}<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">qiu-button</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"icon-plus"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"addTag"</span>&gt;</span>添加标签<span class="hljs-tag">&lt;/<span class="hljs-name">qiu-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    data(){
      <span class="hljs-keyword">return</span>{
         <span class="hljs-attr">tags</span>:[<span class="hljs-number">0</span>,<span class="hljs-number">1</span>]  <span class="hljs-comment">//标签默认数量</span>
      }
    },
    <span class="hljs-attr">methods</span>:{
      addTag(){
        <span class="hljs-keyword">if</span>(<span class="hljs-keyword">this</span>.tags.length){
          <span class="hljs-keyword">this</span>.tags.push(<span class="hljs-keyword">this</span>.tags[<span class="hljs-keyword">this</span>.tags.length<span class="hljs-number">-1</span>]+<span class="hljs-number">1</span>);
        }<span class="hljs-keyword">else</span>{
          <span class="hljs-keyword">this</span>.tags.push(<span class="hljs-number">0</span>);
        }
      },
      closeTag(event,name){
        <span class="hljs-keyword">const</span> index=<span class="hljs-keyword">this</span>.tags.indexOf(name);
        <span class="hljs-keyword">this</span>.tags.splice(index,<span class="hljs-number">1</span>);
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>API</h2>
<h4>qiu-tag属性</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>尺寸，标签的大小</td>
<td>string/Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>颜色，16进制颜色值或其他都可以</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>样式，暂时只支持<code v-pre class="code_inline">border</code></td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>name</td>
<td>标签名称，对<code v-pre class="code_inline">v-for</code>时有用，用于关闭标签</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h4>qiu-tag事件</h4>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-close</td>
<td>标签关闭时触发</td>
<td>event,name</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    data(){
      return{
         tags:[0,1]  //标签默认数量
      }
    },
    methods:{
      addTag(){
        if(this.tags.length){
          this.tags.push(this.tags[this.tags.length-1]+1);
        }else{
          this.tags.push(0);
        }
      },
      closeTag(event,name){
        const index=this.tags.indexOf(name);
        this.tags.splice(index,1);
      }
    }
  }
</script>


