<template><section><h1>Switch 开关</h1>
<hr>
<h3>概述</h3>
<p>在两种状态间切换时用到的开关选择器。</p>
<hr>
<h3>基础用法</h3>
<p>基础用法，状态切换会触发事件</p>
<template>
  <div>
     <QiuSwitch v-model="check" @change="changeStatus"></QiuSwitch>
     <span>{{check}}</span>
  </div>
</template>

<demo-block>
                        <div slot="desc">
<template>
  <div>
     <qiuswitch v-model="check" @change="changeStatus"></qiuswitch>
     <span>{{check}}</span>
  </div>
</template>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
     <span class="hljs-tag">&lt;<span class="hljs-name">QiuSwitch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"check"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"changeStatus"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">QiuSwitch</span>&gt;</span>
     <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{check}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">check</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      changeStatus (status) {
        <span class="hljs-keyword">this</span>.check= status;
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>内嵌文字</h3>
<p>开关内部内容可自定义，例如插入文字或图标，增强视觉效果。通过 slot=&quot;checkedText&quot; 和 slot=&quot;unCheckedText&quot; 完成</p>
<QiuSwitch>
  <span slot="checkedText">开</span>
  <span slot="unCheckedText">关</span>
</QiuSwitch>
<demo-block>
                        <div slot="desc"><template>
  <div>
    <qiuswitch>
      <span slot="checkedText">开</span>
      <span slot="unCheckedText">关</span>
    </qiuswitch>
  </div>
</template>

</div>
