      <span class="hljs-keyword">return</span> {

      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>不同尺寸</h3>
<p>添加属性 size 可设置按钮的尺寸大小，默认提供三种尺寸：small，normal，large</p>
<div>
  <QiuSwitch size="small"></QiuSwitch>
  <QiuSwitch size="small">
    <span slot="checkedText">开</span>
    <span slot="unCheckedText">关</span>
  </QiuSwitch>
  <QiuSwitch size="small" disabled></QiuSwitch>
</div>
<div style="margin-top: 8px;">
  <QiuSwitch></QiuSwitch>
  <QiuSwitch>
    <span slot="checkedText">开</span>
    <span slot="unCheckedText">关</span>
  </QiuSwitch>
  <QiuSwitch disabled></QiuSwitch>
</div>
<div style="margin-top: 8px;">
  <QiuSwitch size="large"></QiuSwitch>
  <QiuSwitch size="large">
    <span slot="checkedText">开</span>
    <span slot="unCheckedText">关</span>
  </QiuSwitch>
  <QiuSwitch size="large" disabled></QiuSwitch>
</div>
<demo-block>
                        <div slot="desc">
<template>
  <div>
    <div>
      <qiuswitch size="small"></qiuswitch>
      <qiuswitch size="small">
        <span slot="checkedText">开</span>
        <span slot="unCheckedText">关</span>
      </qiuswitch>
      <qiuswitch size="small" disabled></qiuswitch>
    </div>
    <div style="margin-top: 8px;">
      <qiuswitch></qiuswitch>
      <qiuswitch>
        <span slot="checkedText">开</span>
        <span slot="unCheckedText">关</span>
      </qiuswitch>
      <qiuswitch disabled></qiuswitch>
    </div>
    <div style="margin-top: 8px;">
      <qiuswitch size="large"></qiuswitch>
      <qiuswitch size="large">
        <span slot="checkedText">开</span>
        <span slot="unCheckedText">关</span>
      </qiuswitch>
      <qiuswitch size="large" disabled></qiuswitch>
    </div>
  </div>
</template>


</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
