      <span class="hljs-tag">&lt;<span class="hljs-name">QiuSwitch</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">QiuSwitch</span>&gt;</span>
